.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.site-list__item {
  position: relative;
  text-align: center;
}

.site-list__link {
  display: block;
  padding: 23px 23px 22px;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .site-list__link {
    padding: 14px 0;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1200px) {
  .site-list__link {
    font-size: 20px;
    line-height: 30px;
    color: inherit;
  }
}
.site-list__link:hover {
  opacity: 0.6;
}
.site-list__link:focus {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .site-list__link--current::after {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 2px;
    background-color: #68b738;
  }
}

.nav__toggle {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 30px;
  height: 30px;
  margin: -4px -2px 0 0;
  color: #000000;
  background-color: transparent;
  border: none;
}
@media (min-width: 768px) {
  .nav__toggle {
    display: none;
  }
}

.nav__toggle-line {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 24px;
  height: 2px;
  background-color: currentColor;
  transform: translate(-50%, -50%);
}
.nav__toggle-line::before, .nav__toggle-line::after {
  content: "";
  position: absolute;
  left: 50%;
  width: 24px;
  height: 2px;
  background-color: currentColor;
  transform: translateX(-50%);
}
.nav__toggle-line::before {
  top: 8px;
}
.nav__toggle-line::after {
  top: -8px;
}

.nav--open .nav__toggle-line {
  background-color: transparent;
}
.nav--open .nav__toggle-line::before {
  top: 50%;
  transform: translate(-35%, -50%) rotate(-46deg);
}
.nav--open .nav__toggle-line::after {
  top: 50%;
  transform: translate(-35%, -50%) rotate(46deg);
}

.nav__list {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 1;
  display: none;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .nav__list {
    position: static;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-end;
    background-color: transparent;
    gap: 33px;
  }
}

.nav--open .nav__list {
  display: flex;
}

