.example__slider {
  position: relative;
}

.slider {
  position: relative;
  width: 100%;
  max-width: 280px;
  margin: 0 auto;
}

.slider__container {
  position: relative;
  min-height: 256px;
  overflow: hidden;
}

.slider__item {
  position: absolute;
  top: 0;
  left: 0;
  width: 50%;
  height: 100%;
  overflow: hidden;
}

.slider__item img {
  display: block;
  width: 100%;
  height: 100%;
}

.slider__item--before {
  z-index: 2;
}

.slider__item--after {
  right: 0;
  left: auto;
  z-index: 1;
}

.slider__item--before img {
  object-position: left;
}

.slider__item--after img {
  object-position: right;
}

.slider__toggle {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 1;
  height: 100%;
  border: 1px solid #666666;
}

.slider__button {
  position: absolute;
  top: 42%;
  left: 43%;
  z-index: 2;
  display: block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.slider__button::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 5px;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-right: 10px solid #666666;
  border-bottom: 10px solid transparent;
}
.slider__button::after {
  content: "";
  position: absolute;
  top: 10px;
  right: 5px;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-right: 10px solid #666666;
  border-bottom: 10px solid transparent;
  transform: rotate(180deg);
}

